@import "~normalize.css";
@font-face {
  font-family: 'Gotham';
  src: url('../assets/fonts/GothamProRegular.ttf') format('truetype');
}
@font-face {
  font-family: 'GothamLight';
  src: url('../assets/fonts/GothamProLightRegular.ttf') format('truetype');
}
.section {
  overflow: hidden;
}
.section_inner {
  padding-left: 5.5rem;
  padding-right: 5.5rem;
  padding-top: 2.6rem;
}
@media (max-width: 960px) {
  .section_inner {
    padding-left: 2rem;
    padding-right: 2rem;
  }
}
@media (max-width: 850px) and (orientation: landscape) {
  .section_inner {
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    padding-top: 1.8rem;
  }
}
@media (max-width: 770px) {
  .section_inner {
    padding-left: 0.5rem;
    padding-right: 0;
    padding-top: 1.8rem;
  }
}
@media (max-width: 650px) {
  .section_inner {
    padding-left: 0;
  }
}
@media (max-width: 480px) {
  .section_inner {
    padding-left: 0;
  }
}
@media (max-width: 390px) {
  .section_inner {
    padding-left: 0;
  }
}
.section_title {
  display: inline-block;
  font-size: 3.1rem;
  font-weight: 300;
  line-height: 3.7rem;
  color: #444444;
  position: relative;
}
.section_title a {
  color: inherit;
  text-decoration: none;
}
.section_title::after {
  content: '';
  display: inline-block;
  width: 0.7rem;
  height: 0.98rem;
  background-image: url(../assets/arrow.svg);
  position: absolute;
  top: calc(50% - 0.3rem);
  right: -2rem;
}
@media (max-width: 480px) {
  .section_title::after {
    right: -13rem;
  }
}
@media (max-width: 390px) {
  .section_title {
    font-size: 2.7rem;
    line-height: 3.3rem;
  }
  .section_title::after {
    right: -8rem;
  }
}
.section_content {
  margin-bottom: 2.5rem;
}
.list_card {
  display: flex;
  gap: 3rem;
  flex-wrap: wrap;
  justify-content: space-between;
}
.card {
  max-width: 27rem;
  height: 38.7rem;
  border: 1px solid #EEEEEE;
  border-radius: 0.5rem;
  padding: 2rem;
  flex: 0 0 25%;
}
@media (max-width: 960px) {
  .card {
    max-width: 24.7rem;
  }
}
@media (max-width: 650px) {
  .card {
    max-width: 24.7rem;
  }
}
@media (max-width: 480px) {
  .card {
    max-width: 35rem;
  }
}
@media (max-width: 390px) {
  .card {
    max-width: 29rem;
  }
}
.card_item {
  display: flex;
  justify-content: center;
  align-items: flex-start;
}
.card_img {
  width: 23rem;
  height: 15rem;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border: none;
  border-radius: 0.5rem;
  display: flex;
  align-items: flex-start;
}
.card_img_1 {
  background-image: url(../assets/marketing/1.png);
}
.card_img_2 {
  background-image: url(../assets/marketing/2.png);
}
.card_img_3 {
  background-image: url(../assets/marketing/3.png);
}
.card_img_4 {
  background-image: url(../assets/marketing/4.png);
}
@media (max-width: 960px) {
  .card_img {
    width: 20.9rem;
  }
}
@media (min-width: 0) and (max-width: 850px) and (orientation: landscape) {
  .card_img {
    width: 20.9rem;
  }
}
@media (max-width: 480px) {
  .card_img {
    width: 24.9rem;
    flex-direction: column;
  }
}
.card_img_text {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #EEFCF6;
  width: 7.8rem;
  height: 2.5rem;
  padding: 0.5rem 1rem;
  border: 1px solid #EEEEEE;
  border-radius: 0.5rem;
  font-size: 1.3rem;
  line-height: 1.5rem;
  font-weight: 400;
  color: #3DDC97;
}
.card_img_text:last-child {
  margin-left: 0.5rem;
  width: 11rem;
  background: #FCF4ED;
  color: #E49F62;
}
@media (max-width: 480px) {
  .card_img_text:last-child {
    margin-left: 0;
    margin-top: 0.5rem;
  }
}
@media (max-width: 960px) {
  .card_img_text:first-child {
    display: none;
  }
}
@media (max-width: 480px) {
  .card_img_text:first-child {
    display: block;
  }
}
.card_icons {
  visibility: hidden;
}
@media (max-width: 480px) {
  .card_icons {
    width: 6rem;
    visibility: visible;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .card_icons_heart {
    width: 2rem;
    height: 1.5em;
    background-image: url(../assets/heart.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    color: #444444;
  }
  .card_icons_line {
    width: 2rem;
    height: 1.5rem;
    background-image: url(../assets/line.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    color: #444444;
  }
}
@media (max-width: 480px) {
  .card_body {
    width: 34rem;
  }
}
@media (max-width: 390px) {
  .card_body {
    width: 29rem;
  }
}
.card_body_text {
  font-size: 1.3rem;
  font-weight: 400;
  line-height: 1.9rem;
  color: #888888;
}
.card_body_title {
  font-size: 1.5rem;
  font-weight: 400;
  line-height: 1.8rem;
  color: #444444;
  margin-top: -0.5rem;
}
.card_body_grade {
  font-size: 1.3rem;
  line-height: 1.5rem;
  color: #00ADEF;
  margin-top: -0.5rem;
  padding-left: 2rem;
  position: relative;
}
.card_body_grade::before {
  content: '';
  display: inline-block;
  width: 1.6rem;
  height: 1.6rem;
  background-image: url(../assets/Star1.svg);
  position: absolute;
  top: calc(50% - 0.8rem);
  left: 0;
}
.card_price {
  margin-top: 3.4rem;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
.card_price_actual {
  font-size: 1.9rem;
  font-weight: 400;
  line-height: 2.3rem;
  color: #444444;
}
.card_price_before {
  font-size: 1.3rem;
  line-height: 1.5rem;
  font-style: normal;
  text-decoration-line: line-through;
  color: #888888;
  margin-right: 2rem;
}
@media (max-width: 960px) {
  .card_price_before {
    display: none;
  }
}
@media (max-width: 480px) {
  .card_price_before {
    display: block;
    margin-right: 8rem;
  }
}
.card_button {
  margin-top: 1.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.card_button_heart {
  width: 2rem;
  height: 1.5em;
  background-image: url(../assets/heart.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  color: #444444;
}
@media (max-width: 480px) {
  .card_button_heart {
    visibility: hidden;
  }
}
.card_button_line {
  width: 2rem;
  height: 1.5rem;
  background-image: url(../assets/line.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  color: #444444;
}
@media (max-width: 480px) {
  .card_button_line {
    visibility: hidden;
  }
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
html {
  font-size: 10px;
}
body {
  font-family: 'Gotham', sans-serif;
  font-size: 16px;
}
body.no-scroll {
  overflow: hidden;
}
.container {
  max-width: 144rem;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  margin: 0 auto;
}
.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 15.2rem;
  height: 4.8rem;
  background: #00ADEF;
  border: none;
  border-radius: 0.5rem;
  font-size: 1.5rem;
  line-height: 1.8rem;
  font-style: normal;
  color: #fff;
  transition: 0.2s all;
}
.btn:hover {
  cursor: pointer;
  background: #3458f5;
}
@media (max-width: 960px) {
  .btn {
    width: 13.1rem;
  }
}
@media (max-width: 480px) {
  .btn {
    width: 100%;
  }
}
@media (max-width: 770px) {
  .slick-list {
    padding-right: 22%;
  }
}
@media (max-width: 650px) {
  .slick-list {
    padding-right: 6%;
  }
}
@media (max-width: 480px) {
  .slick-list {
    padding-right: 5%;
  }
}
@media (max-width: 390px) {
  .slick-list {
    padding-right: 1.5%;
  }
}
.slick-prev {
  display: grid;
  place-items: center;
  background: #F6F6F6;
  width: 2.4rem;
  height: 2.4rem;
  padding: 0.5rem;
  border: none;
  border-radius: 100%;
  top: -3.7rem;
  left: 94%;
}
.slick-prev::before {
  content: '';
  display: inline-block;
  width: 2rem;
  height: 2rem;
  background-image: url(../assets/Arrow_left.png);
  position: absolute;
  top: calc(50% - 1rem);
}
.slick-prev:hover {
  background: #c9c7c7;
}
@media (max-width: 1200px) {
  .slick-prev {
    left: 91%;
  }
}
@media (max-width: 770px) {
  .slick-prev {
    left: 90%;
  }
}
@media (max-width: 650px) {
  .slick-prev {
    left: 83%;
  }
}
@media (max-width: 480px) {
  .slick-prev {
    visibility: hidden;
  }
}
.slick-next {
  display: grid;
  place-items: center;
  background: #F6F6F6;
  width: 2.4rem;
  height: 2.4rem;
  padding: 0.5rem;
  border: none;
  border-radius: 100%;
  right: 1%;
  top: -3.7rem;
}
.slick-next::before {
  content: '';
  display: inline-block;
  width: 2rem;
  height: 2rem;
  background-image: url(../assets/Arrow_right.png);
  position: absolute;
  top: calc(50% - 1rem);
}
.slick-next:hover {
  background: #c9c7c7;
}
@media (max-width: 1200px) {
  .slick-next {
    right: 2%;
  }
}
@media (max-width: 850px) and (orientation: landscape) {
  .slick-next {
    right: 2%;
  }
}
@media (max-width: 650px) {
  .slick-next {
    right: 6%;
  }
}
@media (max-width: 480px) {
  .slick-next {
    visibility: hidden;
  }
}
